
.titleList{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    background: #fff;
    padding: 1px 4px;
    border-radius: 0px;
    box-shadow: 0px 0px 1px rgba(0,0,0,0.1);
    color: #14588d;
    margin-top: 24px;
    margin-bottom: 10px;
    height: 55px;
}

.listWrapper{
    max-height: 700px;
    overflow: auto;
    padding: 10px 5px;
}
.wrapperForScoreCards{
    display: flex;
    flex-direction: column;
    width: 100%;
}
.scoreCardsCont{
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin-bottom: 12px;

}

.scoreCard{
    display: flex;
    align-items: center;
    padding: 4px;
    box-sizing: border-box;
    min-width: 120px;
    justify-content: space-evenly;
    border-radius: 4px;
    box-shadow: 0px 0px 3px 1px rgba(99, 99, 99, 0.2);
    justify-content: space-between;
    transition: all 0.2s ease-in-out;
}

.scoreCard:hover .scoreCircle,.biggerScoreCard:hover .scoreCircle{
    transform: scale(1.1);
    /* background-color: #eee; */
}


.biggerScoreCard{
    display: flex;
    align-items: center;
    padding: 4px;
    box-sizing: border-box;
    min-width: 242px;
    justify-content: space-evenly;
    border-radius: 4px;
    box-shadow: 0px 0px 3px 1px rgba(99, 99, 99, 0.2);
    justify-content: space-between;
    transition: all 0.2s ease-in-out;
}
.scoreCircle{
    font-size: 18px;
    background: #2E294E;
    padding: 3px;
    display: flex;
    justify-content: center;
    border-radius: 4px;
    align-items: center;
    color: #fff;
    box-shadow: 1px 1px 3px 0px rgba(99, 99, 99, 0.2);
    transition: all 0.2s ease-out;
    min-width: 22px;
}

.scoreCard:nth-child(1) .scoreCircle{
    background-color: #2962ff;
}
.scoreCard:nth-child(2) .scoreCircle{
    background-color: #c00028;
}

.scoreCard:nth-child(3) .scoreCircle{
    background-color: #cf9e00;
}

.scoreCard:nth-child(4) .scoreCircle{
    background-color: #008465;
}

.scoreTilte{
    min-width: 75px;
    display: flex;
    justify-content: center;
}


.biggerScoreTilte{
    min-width: 175px;
    display: flex;
    justify-content: center;
}

.mapContainer{
    height: 600px;
}


.updateArea{
    display: flex;
    justify-content: center;
    padding: 20px;
}





.priceCardsList{
    display: flex;
    width: 77%;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.priceCardsContainer{
   display: flex;
   justify-content: center;
}

.promoWrapper{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.dontKnow{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    border: 1px solid #e6e6e6;
    padding: 10px;
    box-shadow: 0px 1px 1px 0px #eee;
    border-radius: 4px;
    margin: 5px 0px;
}
.promoCont{
    display: flex;
    align-items: center;
    width: 80%;
    justify-content: space-between;
    min-height: 30px;
    box-sizing: border-box;
   
}
.promoName{

}

.promoDes{

}

.promoUsage{
    min-width: 49px;
}
/* MEDIA */


@media (max-width:1375px){
    .biggerScoreCard{
       min-width: 0;
        } 
}

@media (max-width:600px){
    .areasList{
        padding-top: 50px;
        }
    .scoreCard{
        flex-direction: column;
        min-width: 0px;
        padding: 6px;
    }
    .scoreTilte{
        min-width: 0px;
    }
    .biggerScoreCard{
        flex-direction: column;
        padding: 6px;
    }

    .biggerScoreTilte{
        min-width: 0px;
        
    }
    
}


@media (max-width:400px){
    .text{
        font-size: 12px;
    }
    .scoreCircle{
        font-size: 12px;
    }
    .dontKnow{
        font-size: 12px;
    }
    
}