.priceCard{
    min-height: 250px;
    min-width: 200px;
    margin: 20px 0px;
    background: #4479a5;
    transition: all 0.2s ease-in;
    padding: 5px;
    box-sizing: border-box;
}

.priceCard:hover{
    background: #4981af;
    transform: translateY(-1px)
}

.service_type{
    display: inline-block;
    padding: 4px;
    background: #fff;
    border-radius: 30px;
    color: rgba(45,117,168,0.9);
}
.logo{
    display: flex;
    justify-content: center;
    height: 75px;
    align-items: center;
}

.logoSize{
    font-size: 40px;
    color: #fff;
}

.rateScope{
    display: flex;
    justify-content: center;
    color: #fff;
}

.priceScope{
    display: flex;
    justify-content: center;
    font-size: 32px;
    height: 75px;
    align-items: center;
    color: #fff;
}

.timeWrapper{
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
}
.tim_distance{
    display: flex;
    align-items: center;
    background: -webkit-linear-gradient(to right, #ece9e6, #ffffff);
    background: linear-gradient(to right, #ece9e6, #ffffff);
    color: rgba(45,117,168,0.9);
    border-radius: 40px;
    padding: 0 10px;
    justify-content: center;
    width: 80%;
    margin-bottom: 4px;
}